import Button from '@material/react-button'
import Card, {
  CardPrimaryContent,
  CardMedia,
  CardActions,
  CardActionButtons,
  CardActionIcons
} from '@material/react-card'
import MaterialIcon from '@material/react-material-icon'
import Link from 'next/link'
import fetch from 'isomorphic-unfetch'


const Movies = ({ shows }) => {
  return (
    <div className='content-container'>
      <h1>Header</h1>
      <p>This content is in <i>pages/index.js</i></p>
      <div
        className='card-container'
      >
        {shows.map(show => (
          <Card
            key={show.id}
          >
            <CardPrimaryContent>
              <CardMedia
                square
                imageUrl='https://picsum.photos/500/500?grayscale'
              />
              <div className='main-content'>
                <h3>
                  <Link href="/p/[id]" as={`/p/${show.id}`}>
                    <a>{show.name}</a>
                  </Link>
                </h3>
              </div>
            </CardPrimaryContent>
            <CardActions>
              <CardActionButtons>
                <Button>
                  Read
                </Button>
              </CardActionButtons>
              <CardActionIcons>
                <MaterialIcon
                  hasRipple
                  icon='share'
                />
              </CardActionIcons>
            </CardActions>
          </Card>
        ))}
      </div>
      <style jsx>{`
        h1 {
          font-weight: 500;
        }
        .content-container {
          padding: 1em;
          max-width: 720px;
          box-sizing: border-box;
          margin: 0 auto;
        }
        .card-container {
          width: 300px;
        }
        .main-content {
          color: rgba(0,0,0,.6);
          padding: 0 1em 0 1em;
        }
      `}</style>
    </div>
  )
}

Movies.getInitialProps = async function() {
  console.log('getting data')
  const res = await fetch('https://api.tvmaze.com/search/shows?q=batman')
  const data = await res.json()
  console.log(`Show data fetched. Count: ${data.length}`)

  return {
    shows: data.map(entry => entry.show)
  }
}

export default Movies
